  border-radius: 30px;
  padding: 6px 18px;
}

button {
  cursor: pointer;
  font-family: inherit;
}

img {
  width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  font-style: normal;
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-color: #ffe550;
  --accent-color: #bbf330;
  --primary-text-color: #2c2c2c;
  --secondary-text-color: #000;
  --additional-text-color: #7eb101;
