  --placeholder-color: #c4c4c4;
  --main-bcg-color: #fefefe;
  --btn-active: #b5ff00;
  --error-color: #ff705d;
  --white-color: #fff;
  --main-font: "Roboto", sans-serif;
}

.header__section {
