}

.logo__icon {
  width: 66px;
  height: 52px;
}

.mob__icon-open {
  width: 23px;
  height: 16px;
  fill: var(--additional-text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.header__btn {
  background: none;
  border: none;
  border-radius: 0;
  padding: 0;
}

.menu-open-btn {
  width: 28px;
  height: 28px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu-open-btn--active .mob__icon-open {
