}

.menu-close-btn {
  width: 24px;
  height: 24px;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 4px;
  right: 4px;
}

.mob__icon-close {
  width: 11px;
  height: 11px;
}

.mob__menu {
  width: 130px;
  background: var(--main-bcg-color);
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  border-radius: 30px 0 30px 30px;
  transition: opacity .25s ease-in-out;
  position: absolute;
  top: 43px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.is-open {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}

.mob__menu-link {
  width: 130px;
  height: 51px;
  color: inherit;
  padding: 16px 0 0 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.19;
