  border-radius: 30px;
  margin-top: 28px;
  padding: 6px 18px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.hero__btn:hover, .hero__btn:focus {
  background: var(--btn-active);
}

.swiper-container {
  display: none;
}

@media screen and (min-width: 768px) {
  .hero__title {
    width: 67%;
    font-size: 42px;
    line-height: 1.2;
  }

  .hero__content {
    margin-top: 44px;
  }

  .hero__text {
    padding-right: 0;
    font-size: 16px;
  }

  .hero__wrapper {
    grid-template-columns: 1fr 1fr;
    gap: 31px;
    display: grid;
  }

  .swiper-container {
    width: 336px;
    height: 320px;
    text-align: center;
    flex-direction: column;
    display: flex;
    overflow: hidden;
  }

  .swiper-slide__circle circle {
    stroke: var(--main-color);
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  }

  .swiper-slide:hover .swiper-slide__circle circle {
    stroke: var(--accent-color);
  }

  .swiper-slide__image {
    width: 294px;
    position: absolute;
    top: 4%;
    left: 6%;
  }

  .swiper-nav {
    z-index: 10;
    display: flex;
    position: absolute;
    top: 367px;
  }

  .swiper__btn {
    width: 34px;
    height: 28px;
    background: var(--white-color);
    border: none;
    border-radius: 4px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
  }

  .swiper-nav__icon path {
    fill: var(--primary-text-color);
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  }

  .swiper__btn:hover .swiper-nav__icon path {
    fill: var(--additional-text-color);
  }

  .swiper__btn:active .swiper-nav__icon path {
    fill: var(--primary-text-color);
  }

  .swiper__btn svg {
    vertical-align: bottom;
  }

  .swiper__btn-prev {
    margin-right: 268px;
  }
}

@media screen and (min-width: 1280px) {
  .section-hero {
    padding: 158px 0;
