  .hero__wrapper {
    gap: 0;
    display: flex;
  }

  .hero__content {
    width: 40%;
    margin-top: 28px;
  }

  .swiper-container {
    width: 604px;
    height: 580px;
    position: absolute;
    top: -144px;
    right: 16px;
  }

  .swiper-slide__circle {
    width: 580px;
    height: 580px;
  }

  .swiper-slide__image {
    width: 528px;
    top: 26px;
    left: 40px;
  }

  .swiper-nav {
    top: 280px;
  }

  .swiper__btn {
    width: 48px;
    height: 40px;
  }

  .swiper-nav__icon {
