.favorites__list {
  list-style: none;
  @include tablet {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 32px;
  }
}

.favorites__title {
  @include font(28px, 700, 1.18);
  text-align: center;
  margin-bottom: 20px;

  @include tablet {
    @include font(42px, 700, 1.17);
  }
}

.favorites__text {
  font-size: 14px;
  line-height: 1.14;
  text-align: center;
  margin-bottom: 60px;

  @include tablet {
    font-size: 16px;
    line-height: 1.19;
  }
  @include desk {
    margin-bottom: 40px;
  }
}

.favorites__item {
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    &:not(:nth-child(3)) {
      margin-bottom: 20px;
    }
  }
}

.favorites__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 282px;
  border: 11px solid var(--main-color);
  border-radius: 50%;
  overflow: hidden;
  @include tablet {
    width: 336px;
    height: 338px;
    border: 14px solid var(--main-color);
  }
  @include desk {
    width: 394px;
    height: 397px;
    border: 16px solid var(--main-color);
  }
}
.favorites__link:hover,
.favorites__link:focus,
.favorites__link:active {
  border-color: var(--accent-color);
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.favorites__item:nth-last-child(-n + 3) {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
